/**
 * @class Request
 */
class Request {

  /**
   * Creates an instance of Request.
   *
   * @param {Hemera} ctx
   *
   * @memberOf Request
   */
  constructor(ctx: Hemera, _request: any) {

    this._ctx = ctx
    this._request = {}
    this.payload = _request
  }

  /**
   *
   *
   * @readonly
   * @type {*}
   * @memberOf Request
   */
  get payload(): any {

    return this._request.value
  }

  /**
   *
   *
   * @readonly
   * @type {*}
   * @memberOf Request
   */
  get error(): any {

    return this._request.error
  }

  /**
   *
   *
   *
   * @memberOf Response
   */
  set payload(value) {

    this._request.value = value
  }

  /**
   *
   *
   *
   * @memberOf Response
   */
  set error(error) {

    this._request.error = error
  }

}

module.exports = Request
